
import { defineComponent } from 'vue'
import {VitalSignCamera} from "vue-vital-sign-camera";
import FaceMeshView from "./FaceMeshView.vue";
import Status from "./StatusView.vue";
import {
  ScanConditions, 
  VideoFrameProcessedEvent, 
  GetHealthResult, 
  VideoFrameInfo,
  NormalizedFacebox, 
  UserInfo, 
  Gender,
ServerId, 
} from "vue-vital-sign-camera";
import ConditionChecklist from "./ConditionChecklist.vue";
import HealthResult from "./HealthResult.vue";
import BoundingBox from "./BoundingBox.vue";
import { NormalizedLandmarkList } from '@mediapipe/drawing_utils'

export default defineComponent( {
  setup() {
    
  },
  data() {
    return {
      isActive: false, // can be used in a button to turn on/off the camera
      healthResult: undefined as GetHealthResult|undefined,
      frameSize: undefined as VideoFrameInfo|undefined,
      facebox: undefined as NormalizedFacebox|undefined,
      conditions: undefined as ScanConditions|undefined,
      userInfo: {
        age: 30,
        gender: Gender.Male,
        userId: '9288f39d-c576-42aa-b9bf-c47c528d7bd0',
      } as UserInfo,
      showResult: false,
      disabled: false,
      config: {
        serverId: ServerId.AwsProdEnterprise,
        apiKey: 'cc86zjHkew5zIFCg8hkkF5TMP3njd2kF1jAhsGHw',
      },
      landmarks: undefined as NormalizedLandmarkList|undefined,
      cameraIndex: 0,
      devices: [{text: "", id: "", deviceIndex: 0}],
      checkDevice: undefined as ReturnType<typeof setInterval>|undefined,
    }
  },
  components: {
    VitalSignCamera,
    Status,
    ConditionChecklist,
    HealthResult,
    BoundingBox,
    FaceMeshView
  },
  methods: {
    startScanning() {
      const vscam:InstanceType<typeof VitalSignCamera> = this.$refs.vscam as InstanceType<typeof VitalSignCamera>;
      vscam.startScanning();
      this.showResult = true;
      // console.log(this.showResult)
    },
    onVideoFrameProcessed(processedVideo:VideoFrameProcessedEvent) {
      const {videoFrameInfo, facebox, healthResult, scanConditions, landmarks} = processedVideo;
      this.conditions = scanConditions;
      this.healthResult = healthResult;
      this.facebox = facebox;
      this.frameSize = videoFrameInfo;
      this.landmarks = landmarks;
      this.disabled = this.conditions ? Object.values(this.conditions).every((item) => item) : false;
      // console.log(videoFrameInfo, facebox, healthResult, scanConditions);
    },
    hideResult() {
      this.showResult = false;
    },
    switchCamera() {
      this.cameraIndex = (this.cameraIndex + 1) % this.devices.length;
    }
  },
  async mounted() {
    this.isActive = true;

    // Checking if there are more than 1 camera connected.
    this.checkDevice = setInterval(async () => {
      let devices = await navigator.mediaDevices.enumerateDevices();
      devices = devices.filter(device => device.kind === 'videoinput');
      this.devices = devices.map((device, index) => {
        return {text: device.label, id: device.deviceId, deviceIndex: index}
      })
      this.cameraIndex = 0;
    }, 1000)
  },
  watch: {
    devices() {
      // Stop checking once all camera devices are detected
      if (this.devices[0].text !== '') {
        clearInterval(this.checkDevice);
        this.checkDevice = undefined;
      }
    }
  }
})
