
import { defineComponent } from 'vue'
import VitalSigns from "./VitalSigns.vue";
import {PropType} from "vue";
import { Health } from 'vue-vital-sign-camera';

export default defineComponent( {
    setup() {
        
    },
    emits: ['hideResult'],
    props: {
        healthResult: Object as PropType<Health>,
        showResult: Boolean,
    },
    components: {
        VitalSigns
    },
    computed: {
        vs() {
            return this.healthResult?.vitalSigns;
        },
        holistic() {
            return this.healthResult?.holisticHealth;
        },
        cardio() {
            return this.healthResult?.risks?.cardiovascularRisks;
        },
        covid() {
            return this.healthResult?.risks?.covidRisk;
        }
    },
    methods: {
        hideResult() {
            this.$emit('hideResult');
        }
    }
})
